body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

html {
  padding: 0px;
}

body {
  padding: 0px;
  background-color: hsl(224, 40%, 70%);
}

.App {
  background-color: hsl(224, 40%, 70%);
}

.intro {
  max-width: 70vw;
  font-size: 20px;
  line-height: 1.4em;
  font-family: monospace;
  background-color: hsla(55, 100%, 65%, 0.8);
}

.header {
  background-color: #fff;
  padding-top: 30px;
  padding-bottom: 20px;
  padding-left: 20px;
  box-shadow: 0px 4px 4px 0px hsla(220, 80%, 10%, 0.4);
  position: -webkit-sticky;
  position: sticky;
  top: 0;
  max-height: 100px;
  display: -webkit-flex;
  display: flex;
}

.header > h1 {
  color: hsl(224, 60%, 10%);
}

.skinny {
  padding: 10px 20px;
}
a {
    text-decoration: none;
    color: black;
}

a:hover {
    background-color: hsla(55, 100%, 65%, 0.8);
}

.repository_row {
  display: grid;
  grid-template-columns: minmax(30vw, 35%) minmax(50vw, 50%)  auto;  
  background: #eeeeee;
  padding: 10px 5px;
}

.repository_name {
    font-weight: bold;
    min-width: 30vw;
}

.repository_description {
    color: hsla(10, 50%, 10%, 0.8);
}

.repository_link > a {
    text-decoration: none;
    color: hsla(10, 50%, 10%, 0.8);
}

.repository_link > a:visited {
    text-decoration: none;
    color: hsla(10, 50%, 10%, 0.8);
}

.repository_link > a:hover {
    color: #000;
    font-weight: bold;
    background-color: hsla(55, 100%, 65%, 0.8);
}

.card {
  background: white;
  margin: 30px 10px;
  border-radius: 4px;
  padding: 10px 5px;
  overflow: hidden;
  box-shadow: 0px 2px 3px 1px rgba(0, 10, 40, 0.5), 0px 2px 10px 5px rgba(0, 10, 50, 0.25);
}

